
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.5em;
}
body a:hover {
  text-decoration: none;
}
.bg-navbar {
  background: #415D9B;
  padding: 0;
  padding-left: 50px;
  padding-right: 259px;
}
.bg-navbar .iconfb {
    width: 25px;
    height: 25px;
}
.bg-navbar .iconfb img {
      margin-bottom: 11px;
      margin-left: 4px;
}
.bg-navbar .search {
    width: 50%;
    font-size: 12px;
}
.bg-navbar .search input {
      border: 0;
      width: 90%;
      padding: 4px;
}
.bg-navbar .search input:focus {
        outline: none;
}
.bg-navbar .search button {
      border: 0;
      cursor: pointer;
      margin-left: -5px;
      padding: 4px 15px;
}
.bg-navbar .collapse .navbar-nav {
    margin-left: 21%;
}
.bg-navbar .collapse .font-size span {
    font-size: 12px;
    font-weight: bold;
}
.bg-navbar .collapse .avatar-navbar {
    vertical-align: middle;
    width: 22px;
    height: 22px;
    border-radius: 50%;
}
.home {
  padding-left: 44px;
  padding-top: 12px;
  margin-top: 40px;
  background: #E9EBEF;
}
.home .sidebar {
    width: 180px;
    position: fixed;
}
.home .sidebar .item {
      padding: 5px;
}
.home .sidebar .item:hover {
        background: #F5F6F7;
}
.home .sidebar .item .icon {
        padding: 2px;
        width: 24px;
}
.home .sidebar .item .name {
        padding-left: 15px;
        color: black;
        font-size: 15px;
}
.home .sidebar .active {
      background: #F5F6F7;
}
.home .sidebar .shortcuts {
      padding: 7px;
      padding-top: 10px;
      font-size: 15px;
      font-weight: bold;
      color: gray;
}
.home .list-post {
    width: 39%;
    margin-left: 187px;
}
.home .list-post .new-post {
      background: white;
}
.home .list-post .new-post .title {
        background: #F5F6F7;
        color: gray;
        cursor: pointer;
        font-size: 14px;
        padding: 6px;
        font-weight: bold;
}
.home .list-post .new-post .content {
        margin: 10px;
        padding-bottom: 15px;
}
.home .list-post .new-post .content img {
          cursor: pointer;
}
.home .list-post .new-post .content input {
          border: 0;
          width: 100%;
          padding-left: 15px;
          padding-top: 9px;
          font-size: 15px;
          color: gray;
}
.home .list-post .new-post .content input:focus {
            outline: none;
}
.home .list-post .new-post .utilities {
        padding: 10px;
}
.home .list-post .new-post .utilities span {
          font-size: 15px;
}
.home .list-post .new-post .utilities .image-video {
          background: #F5F6F7;
          margin-right: 5px;
          border-radius: 14px;
          padding: 3px;
}
.home .list-post .new-post .utilities .image-video a {
            padding: 10px;
}
.home .list-post .new-post .utilities .image-video a .fa-images {
              color: #9FCD6A;
}
.home .list-post .new-post .utilities .image-video a .fa-user-tag {
              color: #4DB3F7;
}
.home .list-post .new-post .utilities .image-video a .fa-grin {
              color: #F2AF70;
}
.home .list-post .new-post .utilities .image-video a span {
              color: black;
}
.home .list-post .new-post .utilities .image-video:hover {
            background: #EBEDF0;
}
.home .list-post .post {
      background: white;
}
.home .list-post .post .content {
        padding: 10px;
}
.home .list-post .post .content img {
          cursor: pointer;
}
.home .list-post .post .content .profile .name {
          font-size: 15px;
          font-weight: bold;
          color: #355899;
}
.home .list-post .post .content .profile .name:hover {
            text-decoration: underline;
}
.home .list-post .post .content .profile .time {
          font-size: 12px;
          color: gray;
}
.home .list-post .post .content .dots {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-pack: end;
              -ms-flex-pack: end;
                  justify-content: flex-end;
          width: 70%;
}
.home .list-post .post .content .dots a {
            color: gray;
}
.home .list-post .post .content .description {
          font-size: 15px;
}
.home .list-post .post .image img {
        width: 100%;
}
.home .list-post .post .interactive {
        padding: 10px;
}
.home .list-post .post .interactive .title .fa-gratipay {
          color: #EF4B50;
          line-height: inherit;
}
.home .list-post .post .interactive .title a {
          font-size: 15px;
          color: gray;
}
.home .list-post .post .interactive .title a:hover {
            text-decoration: underline;
}
.home .list-post .post .interactive .title .title-comment {
          width: 100%;
          -webkit-box-pack: end;
              -ms-flex-pack: end;
                  justify-content: flex-end;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
}
.home .list-post .post .interactive .button button {
          width: 50%;
          cursor: pointer;
          background: white;
          color: gray;
          font-size: 15px;
          border: 0;
          padding: 5px;
          margin: 4px;
          border-radius: 3px;
}
.home .list-post .post .interactive .button button:hover {
            background: #F2F3F2;
}
.home .news {
    background: white;
}
.home .news .title .name {
      font-size: 15px;
      cursor: pointer;
      color: gray;
      font-weight: bold;
}
.home .news .title .name:hover {
        text-decoration: underline;
}
.home .news .title .utilities {
      width: 100%;
      text-align: end;
      font-size: 12px;
}
.home .news .title .utilities a:hover {
        text-decoration: underline;
}
.home .news .new .name {
      margin-bottom: -5px;
}
.home .news .new .name a {
        color: #355899;
        font-size: 14px;
        font-weight: bold;
}
.home .news .new .name a:hover {
          text-decoration: underline;
}
.home .news .new span {
      font-size: 13px;
      color: gray;
}
.home .news .new .img-border {
      border: 2px solid #007bff;
}
.home .birthday {
    background: white;
}
.home .birthday .fa-gift {
      color: #BE5922;
}
.home .birthday span {
      font-size: 13px;
      font-weight: bold;
      color: #355899;
}
.home .friend-request {
    background: white;
}
.home .friend-request .title span {
      font-size: 14px;
      color: gray;
      font-weight: bold;
}
.home .friend-request .title a {
      font-size: 12px;
      width: 63%;
      text-align: end;
}
.home .friend-request .title a:hover {
        text-decoration: underline;
}
.home .friend-request .add-friend .content .name {
      color: #355899;
      font-size: 14px;
      font-weight: bold;
}
.home .friend-request .add-friend .content .name:hover {
        text-decoration: underline;
}
.home .friend-request .add-friend .content .mutual-friend {
      font-size: 12px;
      color: gray;
}
.home .friend-request .add-friend .content .bt a {
      background: #ebecee;
      color: gray;
      font-size: 12px;
      font-weight: bold;
      padding: 4px 10px;
}
.home .friend-request .add-friend .content .bt a:hover {
        background: #e0e3ec;
}
.home .online {
    margin-left: 79%;
    margin-top: -10px;
    width: 19%;
    border-left: 1px solid #dee2e6;
    position: fixed;
    height: 100%;
}
.home .online .item:hover {
      background: #c9cbcf;
      cursor: pointer;
}
.home .online .item .name {
      width: 70%;
      font-size: 13px;
      padding-top: 5px;
}
.home .online .item .icon-online {
      text-align: end;
      font-size: 8px;
      color: green;
      padding-top: 11px;
}
.home .online .search {
      position: fixed;
      bottom: 0;
      background: #F5F6F7;
      width: 18%;
}
.home .online .search i {
        font-size: 12px;
        cursor: pointer;
}
.home .online .search input {
        width: 61%;
        border: 0;
        font-size: 13px;
        padding: 5px;
        background: #F5F6F7;
}
.home .online .search input:focus {
          outline: none;
}
